* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    color: grey;
    font-size: 1.2rem;
    width: 100%;
}

.gif-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.gif {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.gif p {
    padding: 5px;
    max-width:200px;
    text-align: center;
    text-transform: uppercase;
}

.gif img {
    max-height: 170px;
    
}

